import React, {useState, useEffect, useRef} from 'react'
import { Link } from 'react-router-dom'
import { filter, prop } from 'ramda'
import { Grid } from 'gridjs'

import {getCsv} from '../fetch'
import routes from '../routes'

import {SHOWS_URL} from '../data'

import Logger from '../logger'

import './shows_list.css'
import 'gridjs/dist/theme/mermaid.css'

const lgr = new Logger('Shows', {'background-color': 'green'})

const UNDEF = undefined

const hasDate = filter(!!prop('date'))

function GridSample() {
  const wrapperRef = useRef(null)
  useEffect(() => {
    grid.render(wrapperRef.current)
  })
  const grid = new Grid({
    columns: ['Name', 'Email', 'Phone Number'],
    data: [
      ['John', 'john@example.com', '(353) 01 222 3333'],
      ['Mark', 'mark@gmail.com',   '(01) 22 888 4444']
    ]
  })
  return <div ref={wrapperRef} />
}

export default function ShowsList() {
  const [shows, setShows] = useState(UNDEF)
  useEffect(() => {
    getCsv(SHOWS_URL, (data) => setShows(hasDate(data)))
  }, [])
  lgr.log('shows??', shows)
  if (shows === UNDEF) {
    return <p>Loading...</p>
  }
  return <ol className="showslist">
    {shows.length
      ? shows.map(show => <li key={show.id}>
        <Link to={routes.show.replace(':id', show.id)}>{show.date}</Link>
      </li>)
      : <li>Uh oh, no shows found...</li>
    }
    <li><GridSample /></li>
  </ol>
}
